import React from 'react';
import cn from 'classnames';
import styles from './Button.module.css';

const Button = ({ className, round = false, children, ...props }) => (
  <button
    type="button"
    className={cn(styles.button, { [styles.round]: round }, className)}
    {...props}
  >
    {children}
  </button>
);

export default Button;
